.burgerMenuIcon {
    width: 24px;
    height: 24px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    max-width: calc(100% - 140px);
    height: 60px;
}

.headerWrapper {
    box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.header h1 {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
}